.carouselItem {
    display: flex;
    flex-direction: column;
    object-fit: contain;
    padding: 10px;
}

.carouselItem_img {
    max-width:145px;
    max-height:217px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    margin-bottom: 5px;
    box-shadow: 0px 0px 5px black;
}
.carouselItem_img_home {
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    margin-bottom: 5px;
    box-shadow: 0px 0px 5px black;
}
.carouselItem_txt {
    font-size: 1rem;
}
.tray{
    padding: 1px;
}

.genre{
    font-size: 2rem;
    margin: 20px 0;
    font-weight: 500;
}